<template>
  <div class="Sectiontop">
    <h3 class="name-title">جدید ترین محصولات</h3>
    <div>
      <b-card
        v-for="product in products"
        :key="product.id"
        :title="
          product.name.substring(0, 40) +
          (product.name.length > 10 ? '...' : '')
        "
        :img-src="`${product.image_url}`"
        img-alt="Product Image"
        img-top
        tag="article"
        style="width: 350px; height: 500px; font-size: 20px"
        class="mb-2 product-card"
      >
        <router-link class="custom-link" :to="`/vuejs/Product/${product.id}`"
          >بیشتر</router-link
        >
      </b-card>
    </div>
    <!-- <router-link class="custom-link-outline" to="/vuejs/Allproducts"
      >بیشتر</router-link
    > -->
  </div>
</template>

<script>
export default {
  name: "SECTIONTOP",
  data() {
    return {
      products: [],
    };
  },
  methods: {
    async getallproducts() {
      try {
        const response = await fetch(
          "https://backendvue.vercel.app/api/products/get5lastproducts",
          {
            method: "GET",
          }
        );

        if (!response.ok) {
          throw new Error(`خطا در دریافت داده! وضعیت HTTP: ${response.status}`);
        }

        const result = await response.json();
        this.products = result;
      } catch (error) {
        console.error("خطا در دریافت محصولات:", error);
      }
    },
  },
  mounted() {
    this.getallproducts();
  },
};
</script>

<style scoped>
.Sectiontop div {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.name-title {
  margin: 30px 0px 30px 0px;
}
.mb-2 {
  margin: 20px;
}

/* استایل برای لینک‌های اصلی */
.custom-link {
  display: inline-block;
  padding: 10px 20px;
  background-color: #0d6efd;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  font-weight: bold;
  font-size: 15px;
  width: 200px;
  margin-top: 20px;
  transition: background-color 0.3s ease;
}

.custom-link:hover {
  background-color: #0d6efd;
  color: white;
}
.product-card:hover {
  transform: scale(1.05);
}
.product-card title {
  font-size: 14px;
}
.card-img-top{
  width: 100px !important;
}

/* استایل برای لینک با حاشیه */
.custom-link-outline {
  display: inline-block;
  padding: 10px 20px;
  margin-top: 50px;
  background-color: transparent;
  color: #0d6efd;
  text-decoration: none;
  border: 2px solid #0d6efd;
  border-radius: 5px;
  font-weight: bold;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.custom-link-outline:hover,
.custom-link:hover {
  background-color: #28a745;
  color: white;
}
@media screen and (max-width: 600px) {
}
</style>
