<template>
  <div class="home">
    <Navbar />
    <div class="Sectiontop">
      <transition-group name="fade" tag="div">
        <b-card
          v-for="product in products"
          :key="product.id"
          :title="
            product.name.substring(0,25) +
            (product.name.length > 10 ? '...' : '')
          "
          :img-src="`${product.image_url}`"
          img-alt="Product Image"
          img-top
          tag="article"
          style="max-width: 15rem"
          class="mb-2 product-card"
        >
          <router-link class="custom-link" :to="`/vuejs/Product/${product.id}`"
            >بیشتر</router-link
          >
        </b-card>
      </transition-group>
    </div>
    <Footer />
  </div>
</template>

<script>
import Navbar from "../../compoent/Navbar/Navbar.vue";
import Footer from "../../compoent/Foooter/Footer.vue";

export default {
  name: "ALLPRODUCTS",
  components: {
    Navbar,
    Footer,
  },
  data() {
    return {
      products: [], // برای ذخیره محصولات
    };
  },
  methods: {
    async getallproducts() {
      try {
        const response = await fetch(
          "https://backendvue.vercel.app/api/products/getproducts",
          {
            method: "GET",
          }
        );

        if (!response.ok) {
          throw new Error(`خطا در دریافت داده! وضعیت HTTP: ${response.status}`);
        }

        const result = await response.json();
        this.products = result; // ذخیره داده‌های محصولات
      } catch (error) {
        console.error("خطا در دریافت محصولات:", error);
      }
    },
  },
  mounted() {
    this.getallproducts(); // بارگذاری محصولات هنگام بارگذاری کامپوننت
  },
};
</script>

<style scoped>
.home {
  text-align: center;
}
.custom-link {
  display: inline-block;
  margin-top: 20px;
  font-size: 15px;
  width: 200px;
  padding: 10px 20px;
  background-color: #0d6efd;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.custom-link:hover {
  background-color: #0d6efd;
  color: white;
}

.Sectiontop div {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.mb-2 {
  margin: 20px;
}

/* کلاس سفارشی برای کارت محصولات */
.product-card {
  transition: transform 0.3s ease;
}

.product-card:hover {
  transform: scale(1.05);
}

/* انیمیشن Fade In */
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>
