import { createRouter, createWebHistory } from 'vue-router';
import Home from '../components/pages/Home/Home.vue';
// import Adminpanel from '../components/pages/Adminpanel/Adminpanel.vue'
import Test from '../components/Test/Test.vue'
import Account from '../components/pages/Account/Account.vue';
import Allproducts from '../components/compoent/Allproducts/Allproducts.vue'
import Product from '../components/pages/Product/Product.vue'
import Productcategory from '../components/pages/Productcatergory/Productcatergory.vue'
import Search from '../components/pages/Search/Search.vue'
import Card from '../components/pages/card/card.vue'
const routes = [
  { path: '/vuejs/', component: Home },
  // { path: '/vuejs/Adminpanel', component: Adminpanel },
  { path: '/vuejs/Test', component: Test },
  { path: '/vuejs/Account', component: Account },
  { path: '/vuejs/Allproducts', component: Allproducts },
  { path: '/vuejs/Product/:id', component: Product },
  { path: '/vuejs/card', component: Card },
  { path: '/vuejs/Search/:name', component: Search },
  { path: '/vuejs/Productcategory/:category', component: Productcategory },
  
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router
